.menu-m-panel__overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  transition: all 0.25s ease-in-out;
}

.menu-m-panel {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s ease-in-out;
}

.menu-m-panel.open {
  display: block !important;
  opacity: 1;
  visibility: visible;
}

.menu-m-panel.open .menu-m-panel__overlay {
  opacity: 1;
  visibility: visible;
}

.menu-m-panel.open .menu-m-panel__panel {
  opacity: 1;
  visibility: visible;
}

.menu-m-panel .close {
  position: absolute;
  top: 15px;
  right: 15px;
}

.menu-m-panel__panel {
  background-color: #fff;
  z-index: 1000;
  position: fixed;
  top: 0;
  height: 100%;
  left: 0;
  width: 100%;
  transition: all 0.25s ease-in-out;
}

.menu-m-panel__hd {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  z-index: 1;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.menu-m-panel__cont {
  height: 100%;
  padding-top: 60px;
  padding-bottom: 60px;
}

.menu-m-panel__inner {
  max-height: 100%;
  overflow: auto;
  padding: 15px;
}

@media only screen and (max-width: 479px) {
  .menu-m-panel__panel {
    width: 100%;
  }
}

@media only screen and (min-width: 480px) and (max-width: 1199px) {
  .menu-m-panel.open .menu-m-panel__panel {
    transform: translateX(0);
  }

  .menu-m-panel__panel {
    width: 300px;
    transform: translateX(-100%);
  }
}